body {
  background-color: #2c2c2c;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

h1 {
  color: #fff;
}

.stopwatch {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.numbers {
  color: #fff;
  font-size: xx-large;
}

button {
  color: #fff;
  border-style: solid;
  border-color: #fff;
  border-radius: 16px;
  margin: 8px;
  padding: 8px 16px;
  font-size: large;
}

button:disabled {
  color: #d2d2d2;
  background-color: #b4b4b4;
  border-color: #d2d2d2;
}

button:hover {
  filter: brightness(120%);
}

.start {
  background-color: #31b831;
}

.stop {
  background-color: #c81a1a;
}

